export type Dictionary<T> = { [key: string]: T };
type ByFunc<T> = (item: T) => string;

export function sortBy<T>(array: T[], ...by: Array<ByFunc<T>>): T[] {
  return internalSortBy(array, false, ...by);
}
export function sortByDesc<T>(array: T[], ...by: Array<ByFunc<T>>): T[] {
  return internalSortBy(array, true, ...by);
}

function internalSortBy<T>(array: T[], descending: boolean, ...by: Array<ByFunc<T>>): T[] {
  return array.sort((item1, item2) => {
    const descFactor = descending ? -1 : 1;
    for (const byFunc of by) {
      const val1 = byFunc(item1);
      const val2 = byFunc(item2);

      if (val1 < val2) return -1 * descFactor;
      else if (val1 > val2) return 1 * descFactor;
    }
    return 0;
  });
}

export function toDictionary<T, V = T>(
  arr: T[],
  keyFunc: ByFunc<T>,
  valFunc?: (item: T) => V,
  allowMultipleKeys: boolean = false
): Dictionary<V> {
  return arr.reduce((obj, val) => {
    const key = keyFunc(val);
    if (obj[key] && !allowMultipleKeys) throw Error("key already exists");

    obj[key] = valFunc ? valFunc(val) : val;

    return obj;
  }, {});
}
export function toLookup<T>(arr: T[], keyFunc: ByFunc<T>) {
  return toDictionary(arr, keyFunc, _ => true, true);
}

export function groupBy<T>(arr: T[], byFunc: ByFunc<T>): Dictionary<T[]> {
  return arr.reduce((obj, val) => {
    const key = byFunc(val);
    if (!obj[key]) {
      obj[key] = [];
    }
    obj[key].push(val);

    return obj;
  }, {});
}

export function distinct<T>(arr: readonly T[], byFunc?: ByFunc<T>): T[] {
  const used = {};
  const result = [];
  for (const val of arr) {
    const key = byFunc ? byFunc(val) : String(val);
    if (!used[key]) {
      result.push(val);
      used[key] = true;
    }
  }
  return result;
}

export class Linq<T> {
  arr: T[];
  constructor(arr: T[]) {
    this.arr = arr;
  }

  concat(otherArr: T[]) {
    return new Linq(this.arr.concat(otherArr));
  }
  filter(condition: (item: T) => boolean) {
    return new Linq(this.arr.filter(condition));
  }
  map<V>(mapFunc: (item: T) => V) {
    return new Linq(this.arr.map(mapFunc));
  }
  orderBy(...byFunc: Array<ByFunc<T>>) {
    const arrCopy = [...this.arr]; // avoid mutating array
    return new Linq(sortBy(arrCopy, ...byFunc));
  }
  orderByDesc(...byFunc: Array<ByFunc<T>>) {
    const arrCopy = [...this.arr]; // avoid mutating array
    return new Linq(sortByDesc(arrCopy, ...byFunc));
  }
  distinct(byFunc?: ByFunc<T>) {
    return new Linq(distinct(this.arr, byFunc));
  }

  groupBy(byFunc: ByFunc<T>) {
    return groupBy(this.arr, byFunc);
  }
  toArray() {
    return this.arr;
  }
  toDictionary<V = T>(keyFunc: ByFunc<T>, valFunc?: (item: T) => V) {
    return toDictionary(this.arr, keyFunc, valFunc);
  }
  toLookup(keyFunc: ByFunc<T>) {
    return toLookup(this.arr, keyFunc);
  }
}

// modified https://github.com/simontonsoftware/micro-dash/blob/master/lib/src/lang/is-equal.ts
// to ignore own properties (i.e. vue's reactive properties)
export function isEqual(value: any, other: any, compareFunc?: (v1: any, v2: any) => boolean | null) {
  if (compareFunc) {
    const result = compareFunc(value, other);
    if (typeof result === "boolean") {
      return result;
    }
  }

  if (value === other) {
    return true;
  }
  if (!(value instanceof Object && other instanceof Object)) {
    return false;
  }

  if (value instanceof Array) {
    if (!(other instanceof Array) || value.length !== other.length) {
      return false;
    }
    for (let i = 0; i < value.length; i++) {
      if (!isEqual(value[i], other[i], compareFunc)) {
        return false;
      }
    }
  } else {
    for (const key of Object.keys(value)) {
      if (!other.hasOwnProperty(key)) {
        return false;
      }
    }
    for (const key of Object.keys(other)) {
      if (!isEqual(value[key], other[key], compareFunc)) {
        return false;
      }
    }
  }

  return true;
}

export function moveItem(arr: any[], fromIndex: number, toIndex: number) {
  if (fromIndex < 0 || fromIndex >= arr.length || toIndex < 0 || toIndex >= arr.length) {
    throw Error("Invalid indexes for moveItem");
  }
  const item = arr.splice(fromIndex, 1)[0];
  arr.splice(toIndex, 0, item);
}
