import { ISeason, Dictionary } from "root/data-types";
import { computed, reactive, Ref, ref } from "@vue/composition-api";
import { Linq } from "./utils/object-utils";

export interface IFlatSeason {
  name: string;
}
export interface IFlatAgeGroup {
  season: string;
  name: string;
}
export interface IFlatTeam {
  season: string;
  ageGroup: string;
  name: string;
}

export interface IFlatPlayer {
  season: string;
  ageGroup: string;
  team: string;
  name: string;
}

export interface IFlatTeamMatch {
  season: string;
  ageGroup: string;
  team: string;
  player: string;

  teamMatchKey: string;
  teamMatchWon: boolean;
  isHomeTeam: boolean;
}

export interface IFlatMatch extends IFlatTeamMatch {
  matchKey: string;
  category: string;
  discipline: string;
  isMatchWon: boolean;
  isThreeSets: boolean;
  isWalkover: boolean;
  isGoldenSet: boolean;
}

export interface IModel {
  view: ViewOption;
  categories: string[];
  includeWO: boolean;
  includeGoldenSet: boolean;
  seasons: string[];
  ageGroups: string[];
  teams: string[];
  player: string;
  wonTeamMatch: string;
  isHomeTeam: string;
  wonMatch: string;
  isThreeSets: string;
  groupByFirst: string;
  groupBySecond: string;
}

export interface IForInput {
  key: keyof IModel;
  label: string;
  items: readonly string[];
  isSingle?: boolean;
}
export interface IWhereInput {
  key: keyof IModel;
  label: string;
  items: Array<{ text: string; value: string }>;
  isMatchOnly?: boolean;
}

export interface IGroupByItem {
  text: string;
  value: string;
  values: readonly string[];
  groupBy: (match: IFlatTeamMatch | IFlatMatch) => string;
}
export const enum ViewOption {
  TeamMatch = "TeamMatch",
  Match = "Match"
}

export function useDataInputs() {
  const model: Ref<IModel> = ref(getNewModel());

  const {
    filteredSeasons,
    filteredAgeGroups,
    filteredTeams,
    filteredPlayers,
    filteredTeamMatches,
    filteredMatches
  } = getFilterdData(model);

  const dataLoaded = computed(() => filteredSeasons.value.length > 0);

  const viewOptions = [
    { text: "Holdkampe", value: ViewOption.TeamMatch },
    { text: "Enkeltkampe", value: ViewOption.Match }
  ];
  const categories = ref(["MD", "DS", "HS", "DD", "HD"]);

  const forInputs = computed<IForInput[]>(() => [
    { key: "seasons", label: "Sæson(er)", items: filteredSeasons.value },
    { key: "ageGroups", label: "Aldersgruppe(r)", items: filteredAgeGroups.value },
    { key: "teams", label: "Hold", items: filteredTeams.value },
    { key: "player", label: "Spiller", items: filteredPlayers.value, isSingle: true }
  ]);

  const booleanItems = [
    { text: "", value: null },
    { text: "Ja", value: "true" },
    { text: "Nej", value: "false" }
  ];
  const whereInputs = computed<IWhereInput[]>(() => [
    { key: "wonTeamMatch", label: "Holdkamp vundet", items: booleanItems },
    { key: "isHomeTeam", label: "Hjemmebane", items: booleanItems },
    { key: "wonMatch", label: "Kamp vundet", items: booleanItems, isMatchOnly: true },
    { key: "isThreeSets", label: "I 3 sæt", items: booleanItems, isMatchOnly: true }
  ]);

  const groupByItems = computed(() => {
    const options: IGroupByItem[] = [
      { text: "Sæson", value: "season", values: filteredSeasons.value, groupBy: m => m.season },
      { text: "Aldersgruppe", value: "ageGroup", values: filteredAgeGroups.value, groupBy: m => m.ageGroup },
      { text: "Hold", value: "team", values: filteredTeams.value, groupBy: m => m.team },
      {
        text: "Vundet/tabt holdkamp",
        value: "teamMatchWon",
        values: ["Vundet holdkamp", "Tabt holdkamp"],
        groupBy: m => (m.teamMatchWon ? "Vundet holdkamp" : "Tabt holdkamp")
      },
      {
        text: "Hjemmebane/udebane",
        value: "isHomeTeam",
        values: ["Hjemmebane", "Udebane"],
        groupBy: m => (m.isHomeTeam ? "Hjemmebane" : "Udebane")
      }
    ];
    if (model.value.view === ViewOption.Match) {
      options.push(
        {
          text: "Kategori",
          value: "category",
          values: categories.value,
          groupBy: m => (m as IFlatMatch).category
        },
        {
          text: "Vundet/tabt kamp",
          value: "isMatchWon",
          values: ["Vundet kamp", "Tabt kamp"],
          groupBy: m => ((m as IFlatMatch).isMatchWon ? "Vundet kamp" : "Tabt kamp")
        }
      );
    }
    return options;
  });

  const getResult = () => {
    const groupByFirstOption = groupByItems.value.find(i => i.value === model.value.groupByFirst);
    const groupBySecondOption = groupByItems.value.find(i => i.value === model.value.groupBySecond);

    const graphData: any[][] = [];

    const firstGroupName =
      groupByFirstOption.value === "season" ||
      groupByFirstOption.value === "ageGroup" ||
      groupByFirstOption.value === "team"
        ? groupByFirstOption.text
        : "";

    const matches = model.value.view === ViewOption.TeamMatch ? filteredTeamMatches.value : filteredMatches.value;
    const firstGroupData = new Linq([...matches]).groupBy(groupByFirstOption.groupBy);

    if (groupBySecondOption) {
      graphData.push([firstGroupName, ...groupBySecondOption.values]);

      for (const value of groupByFirstOption.values) {
        const secondGroupData = firstGroupData[value]
          ? new Linq(firstGroupData[value]).groupBy(groupBySecondOption.groupBy)
          : {};
        graphData.push([value, ...groupBySecondOption.values.map(v => secondGroupData?.[v]?.length ?? 0)]);
      }
    } else {
      graphData.push([firstGroupName, ""]);

      for (const value of groupByFirstOption.values) {
        graphData.push([value, firstGroupData?.[value]?.length ?? 0]);
      }
    }

    return graphData;
  };

  return {
    dataLoaded,
    model,

    viewOptions,
    categories,
    forInputs,
    whereInputs,

    groupByItems,

    getResult
  };
}

export function getNewModel(defaultValues?: Dictionary<any>): IModel {
  const getBool = (val: string) => val === "true";
  const getArray = (val: string[] | string) => (typeof val === "string" ? [val] : val);
  return {
    view: defaultValues?.view || ViewOption.TeamMatch,
    categories: getArray(defaultValues?.categories) || [],
    includeWO: getBool(defaultValues?.includeWO) || false,
    includeGoldenSet: getBool(defaultValues?.includeGoldenSet) || false,
    seasons: getArray(defaultValues?.seasons) || [],
    ageGroups: getArray(defaultValues?.ageGroups) || [],
    teams: getArray(defaultValues?.teams) || [],
    player: defaultValues?.player || null,
    wonTeamMatch: defaultValues?.wonTeamMatch || null,
    isHomeTeam: defaultValues?.isHomeTeam || null,
    wonMatch: defaultValues?.wonMatch || null,
    isThreeSets: defaultValues?.isThreeSets || null,
    groupByFirst: defaultValues?.groupByFirst || "season",
    groupBySecond: defaultValues?.groupBySecond || null
  };
}

function getFilterdData(model: Ref<IModel>) {
  const seasons = ref<IFlatSeason[]>([]);
  const ageGroups = ref<IFlatAgeGroup[]>([]);
  const teams = ref<IFlatTeam[]>([]);
  const players = ref<IFlatPlayer[]>([]);
  const teamMatches = ref<IFlatTeamMatch[]>([]);
  const matches = ref<IFlatMatch[]>([]);

  (async () => {
    const data = await getFlattenData();
    seasons.value = data.seasons;
    ageGroups.value = data.ageGroups;
    teams.value = data.teams;
    players.value = data.players;
    teamMatches.value = data.teamMatches;
    matches.value = data.matches;
  })();

  const filteredSeasons = computed(() => seasons.value.map(s => s.name).sort());
  const filteredAgeGroups = computed(() =>
    ageGroups.value
      .filter(ag => model.value.seasons.length === 0 || model.value.seasons.some(s => s === ag.season))
      .map(ag => ag.name)
      .sort()
  );

  const filteredTeams = computed(() =>
    new Linq(teams.value)
      .filter(
        t =>
          (model.value.seasons.length === 0 || model.value.seasons.some(s => s === t.season)) &&
          (model.value.ageGroups.length === 0 || model.value.ageGroups.some(ag => ag === t.ageGroup))
      )
      .orderBy(
        t => t.ageGroup,
        t => t.name
      )
      .map(t => t.name)
      .distinct()
      .toArray()
  );

  const filteredPlayers = computed(() =>
    new Linq(players.value)
      .filter(
        p =>
          p.name[0] !== "(" &&
          (model.value.seasons.length === 0 || model.value.seasons.some(s => s === p.season)) &&
          (model.value.ageGroups.length === 0 || model.value.ageGroups.some(ag => ag === p.ageGroup)) &&
          (model.value.teams.length === 0 || model.value.teams.some(t => t === p.team))
      )
      .map(p => p.name)
      .distinct()
      .orderBy(p => p)
      .toArray()
  );

  const filterMatch = (m: IFlatTeamMatch) => {
    return (
      (model.value.seasons.length === 0 || model.value.seasons.some(s => s === m.season)) &&
      (model.value.ageGroups.length === 0 || model.value.ageGroups.some(ag => ag === m.ageGroup)) &&
      (model.value.teams.length === 0 || model.value.teams.some(t => t === m.team)) &&
      (model.value.player === null || model.value.player === m.player) &&
      (model.value.wonTeamMatch === null || model.value.wonTeamMatch === String(m.teamMatchWon)) &&
      (model.value.isHomeTeam === null || model.value.isHomeTeam === String(m.isHomeTeam))
    );
  };

  const filteredTeamMatches = computed(() => {
    return new Linq(teamMatches.value)
      .filter(filterMatch)
      .distinct(m => m.teamMatchKey)
      .toArray();
  });
  const filteredMatches = computed(() => {
    return new Linq(matches.value)
      .filter(filterMatch)
      .filter(
        m =>
          (model.value.categories.length === 0 || model.value.categories.indexOf(m.category) >= 0) &&
          (model.value.includeWO || !m.isWalkover) &&
          (model.value.includeGoldenSet || !m.isGoldenSet) &&
          (model.value.wonMatch === null || model.value.wonMatch === String(m.isMatchWon)) &&
          (model.value.isThreeSets === null || model.value.isThreeSets === String(m.isThreeSets))
      )
      .distinct(m => m.matchKey)
      .toArray();
  });

  return {
    filteredSeasons,
    filteredAgeGroups,
    filteredTeams,
    filteredPlayers,
    filteredTeamMatches,
    filteredMatches
  };
}

async function getFlattenData() {
  const seasons: IFlatSeason[] = [];
  const ageGroups: IFlatAgeGroup[] = [];
  const teams: IFlatTeam[] = [];
  const players: IFlatPlayer[] = [];
  const teamMatches: IFlatTeamMatch[] = [];
  const matches: IFlatMatch[] = [];

  const seasonData = await loadSeasons();

  for (const season of seasonData) {
    seasons.push({ name: season.name });

    for (const [_, ageGroup] of Object.entries(season.ageGroups)) {
      ageGroups.push({
        name: ageGroup.name,
        season: season.name
      });

      for (const [__, team] of Object.entries(ageGroup.teams)) {
        const teamName = team.name + ` (${ageGroup.name})`;
        teams.push({
          name: teamName,
          season: season.name,
          ageGroup: ageGroup.name
        });

        const teamPlayers: string[] = [];

        for (const teamMatch of team.teamMatches) {
          const matchPlayers: string[] = [];

          for (const match of teamMatch.matches) {
            for (const player of match.players) {
              const isNewMatchPlayer = matchPlayers.indexOf(player) === -1;

              const flatTeamMatch: IFlatTeamMatch = {
                season: season.name,
                ageGroup: ageGroup.name,
                team: teamName,
                player,
                teamMatchKey: String(teamMatch.matchId),
                isHomeTeam: teamMatch.isHomeTeam,
                teamMatchWon: teamMatch.result[0] > teamMatch.result[1]
              };
              const flatMatch: IFlatMatch = {
                ...flatTeamMatch,
                matchKey: flatTeamMatch.teamMatchKey + "_" + match.discipline,
                discipline: match.discipline,
                category: match.discipline.split(" ").slice(-1)[0],

                isMatchWon: match.isWinner,
                isThreeSets: match.score.length === 3,
                isWalkover: match.isWO,
                isGoldenSet: match.isGoldenSet
              };

              if (isNewMatchPlayer) {
                matchPlayers.push(player);
                teamPlayers.push(player);
                teamMatches.push(flatTeamMatch);
              }
              matches.push(flatMatch);
            }
          }
        }

        players.push(
          ...new Linq(teamPlayers)
            .distinct()
            .map(p => ({
              name: p,
              season: season.name,
              ageGroup: ageGroup.name,
              team: teamName
            }))
            .toArray()
        );
      }
    }
  }

  return {
    seasons,
    ageGroups,
    teams,
    players,
    teamMatches,
    matches
  };
}

async function loadSeasons() {
  // @ts-ignore
  const context = require.context("../../output", true, /.*\.json$/, "lazy");

  const seasons: ISeason[] = await Promise.all(context.keys().map(key => context(key)));
  return seasons;
}
