import { computed, createComponent, ref, onMounted, watch } from "@vue/composition-api";
import { useDataInputs, ViewOption, getNewModel } from "./data-service";
import qs from "qs";

const App = createComponent({
  setup() {
    const { isChartReady, chartEl, drawChart, clearChart } = useCharts();

    const {
      dataLoaded,
      viewOptions,
      categories,
      model,
      forInputs,
      whereInputs,
      groupByItems,
      getResult
    } = useDataInputs();

    const groupBySecondItems = computed(() => {
      return [{ text: "", value: null }].concat(groupByItems.value);
    });

    const buildGraph = () => {
      clearChart();

      const result = getResult();
      const title = model.value.view === ViewOption.TeamMatch ? "Holdkampe" : "Enkeltkampe";
      drawChart(title, result);

      const url = window.location.href.replace(window.location.search, "") + "?" + qs.stringify(model.value);
      window.history.replaceState(null, "", url);
    };

    const clearData = () => {
      model.value = getNewModel();
      clearChart();
      window.history.replaceState(null, "", window.location.href.replace(window.location.search, ""));
    };

    onMounted(() => {
      window.addEventListener("resize", debounce(buildGraph, 500));
    });

    watch([dataLoaded, isChartReady], ([val, val2]) => {
      if (val && val2) {
        const query = window.location.search.replace("?", "");
        if (query && query !== qs.stringify(model.value)) {
          model.value = getNewModel(qs.parse(query));
          buildGraph();
        }
      }
    });

    return {
      dataLoaded,
      model,

      viewOptions,
      categories,
      forInputs,
      whereInputs,
      groupByItems,
      groupBySecondItems,

      isChartReady,
      chartEl,

      buildGraph,
      clearData
    };
  }
});

export default App;

function useCharts() {
  const isChartReady = ref(false);
  const chartEl = ref(null);
  const chart = ref<google.visualization.ColumnChart>(null);

  (() => {
    google.charts.load("current", { packages: ["corechart", "bar"] });
    google.charts.setOnLoadCallback(() => {
      isChartReady.value = true;
    });
  })();

  const drawChart = (title: string, data: any[][]) => {
    const chartData = google.visualization.arrayToDataTable(data);
    const mobile = isMobile();

    const options: any = {
      title,
      chartArea: { left: 50, top: 40, right: mobile ? 30 : 150, width: "100%", height: "80%" },
      legend: { position: mobile ? "top" : "right" },
      vAxis: { format: "#", minValue: 0 },
      hAxis: {
        title: data[0][0]
      },
      backgroundColor: "rgb(250, 250, 250)"
    };

    if (data[0].length === 2) {
      options.legend = { position: "none" };
      options.chartArea.right = 0;
    }

    chart.value = new google.visualization.ColumnChart(chartEl.value);
    chart.value.draw(chartData, options);
  };

  const clearChart = () => {
    if (chart.value) chart.value.clearChart();
  };

  return {
    isChartReady,
    chartEl,
    drawChart,
    clearChart
  };
}

function isMobile() {
  return window.matchMedia("(max-device-width: 600px)").matches;
}

function debounce(func: () => void, time: number) {
  let timeout: number = null;
  return () => {
    clearTimeout(timeout);
    timeout = setTimeout(func, time);
  };
}
